.root{
    :global{
        margin-top: 425px;
        margin-left: 98px;
        display: flex;
        flex-direction: column;
        gap: 75px;

        .row1{
            display: flex;
            flex-direction: row;
            gap: 217px;

            .col1{
                margin-top: 40px;

                img:nth-of-type(1){
                    position: absolute;
                    z-index: -1;
                    margin-left: 51px;
                    margin-top: -83px;
                }
                img:nth-of-type(3){
                    position: absolute;
                    z-index: -1;
                    margin-top: 154px;
                    margin-left: -486px;
                }
            }
            .col2{
                display: flex;
                flex-direction: column;
                gap: 30px;

                h1{
                    font-family: 'Widock TRIAL';
                    font-size: 48px;
                    line-height: 56px;
                    color: #fff;
                }
                .row{
                    display: flex;
                    flex-direction: row;
                    gap: 41px;

                    img{
                        transition: 1s;
                    }
                    img:active{
                        transform: translateX(-150px) scale(2);
                    }
                }
            }
        }
        .row2{
            margin-left: 59px;
            p{
                width: 1454px;
                font-family: 'Kreadon';
                font-size: 24px;
                line-height: 30px;
                color: #fff;
            }
        }
    }
}